.allDialogs{
    display: flex;
    .dialogs{
        width: 40%;
        border-right: 1px solid #ccc;
        padding:0px 10px 10px 0;
        .dialog{
            padding:10px 20px;
            text-align: center;
            border:1px solid #ccc;
            margin-bottom: 10px;
            border-radius: 3px;
            font-size:17px;
            font-family: Tahoma, sans-serif;
            cursor: pointer;

            &:hover{
                background: #ccc;
            }
        }
    }

    .messages{
        padding-left: 10px;
        width: 40%;
        
        .message{
            

            .messageName{
                margin-bottom: 10px;
                font-size: 16px;
                font-weight: bold;
                display:block;
            }

            .messageContent{
                width: 100%;
                padding:10px 20px;
                text-align: center;
                border:1px solid #ccc;
                margin-bottom: 10px;
                border-radius: 3px;
                font-size:17px;
                font-family: Tahoma, sans-serif;
                cursor: pointer;
                &:hover{
                    background: #ccc;
                }
            }

            
        }
        .textMessage{
            display:block;
            font-size:16px;
            color:rgb(105, 105, 105);
            width: 100%;
            margin:0;
            background: none;
            border:none;
            resize: none;
            box-shadow:1px 1px 3px rgba(0,0,0,.4);
            margin-top: 30px;
            border-radius: 5px;
            padding:5px 10px;
            transition: all .2s ease;
            cursor: pointer;
            outline:none;
            margin-top: 30px;
        }

        .buttonSent{
            display:block;
            font-size:16px;
            width: 150px;
            background: none;
            border:none;
            margin-top: 10px;
            border-radius: 5px;
            padding:5px 10px;
            transition: all .2s ease;
            cursor: pointer;
            box-shadow:1px 1px 3px rgba(0,0,0,.4);
            outline:none;

            &:hover{
                background: rgb(113, 172, 255);
                color:#fff;
                box-shadow:0px 0px 2px rgba(0,0,0,.9);
                

            }
        }
    }
}




